@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);

body {
    margin: 0px;
    padding: 0px;
    background-color: #14182A;
}
html {
    font-family: "Open Sans","HelveticaNeue","Helvetica Neue",Helvetica,Arial,sans-serif;
}
*:focus {
    outline: none;
}

li {list-style: none;}
a { text-decoration: none; }

.navTop {
    width: 100%;
    height: 60px;
    background: #1B2039;
    position: fixed;
    box-shadow: 0px 1px rgba(60, 200, 231, .3);
}

.navTopCenter {
    width: 50%;
    height: 60px;
    display: inline-flex;
    align-items: center;
    color: white;
    vertical-align: super;
}
.searchBar {
    padding-left: 6px;
    width: 70%;
    margin-left: 20%;
    display: flex;
    flex-flow: row nowrap;
    color: #737373;
    background-color: #F7F7F7;
    border-radius: 40px;
}
.searchBar input {
    padding: 7px;
    background-color: #F7F7F7;
    border: none;
    width: 80%;
}
.searchBarIcon {
    padding: 8px;
    line-height: 8px;
    color: #3BC8E7;
}


.navTopRight {
    width: 45%;
    height: 60px;
    display: inline-flex;
    color: #ffffff;
    justify-content: flex-end;
    vertical-align: super;
}
.navTopRight li {
    margin-right: 20px;
    display: inline-flex;
}
.navTopRight li button {
    border: none;
    background: transparent;
    font-size: 19px;
    color: #ffffff;
    display: inline-flex;
    align-items: center;
    vertical-align: -webkit-baseline-middle;

}
.navTopRight li button .badge {
    font-size: 8px;
    padding: 5px 10px;
    margin-top: -8px;
    margin-left: 11px;
    border-radius: 2px;
    color: #fff;
    position: absolute;
    background: #3BC8E7;
}
.navTopRight li button img {
    border-radius: 100%;
    height: 25px;
    width: 25px;
    border: 2px solid white;
}

.navTopRight li button small {
    color: white;
    font-size: 12px;
    margin-left: 20px;
    margin-right: 5px;
}

.navTopRight li button small:hover {
    text-decoration: underline;
    color: #3BC8E7;
    cursor: pointer;
}

/* DropDowns */
.dropDownMenuUser {
    width: 90%;
    display: flex;
    border: 1px solid #E9E9E9;
    margin: -2px auto;
    z-index: 1000;    
    left: 50%;
    transform: translateX(-50%);
    position: fixed;
    border-radius: 0px 0px 10px 10px;
    height: 30px;
    background-color: #F7F7F7;
}
.dropDownMenuUser li {
    display: inline-flex;
    vertical-align: middle;
    align-items: center;
    margin-right: 0px;
    color: #737373;
    font-size: 20px;
    margin-left: 10px;
}
.dropDownMenuUser span:hover {
    color: #FA7500;
    text-decoration: underline;
}
.closeDropDown {
    color: red!important;
}
.closeDropDown span {
    color: red!important;
    margin-right: 25px;
    display: inline-block;
    font-size: 14px;
    color: #737373;
    margin-left: 5px;
    cursor: pointer;
}
.dropDownMenuUser span:hover {
    text-decoration: underline;
}
*:focus {
    outline: none;
}

li {list-style: none;}
a { text-decoration: none; }


hr {
    border: 1px solid rgba(200,200,200, .2);
}
.sideNavSolid {
    width: 20%;
    min-height: 100vh;
}
.sideNav {
    border-right: 1px solid rgba(60, 200, 231, .3);
    z-index: 1;
    padding-top: 10px;
    max-height: 100vh;
    min-height: 100vh;
    padding-bottom: 60px;
    background-color: rgb(27, 32, 57);
    overflow-y: scroll;
    display: inline-table;
    position: fixed;
}

.sideNav-title {
    color: #ffffff;
    letter-spacing: .5625pt;
    padding: 15px 25px;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 600;
}
.sideNav li {
    font-size: .875pc;
    align-items: center;
    color: #ffffff;
    display: flex;
    padding: 15px;
    justify-content: center;
    font-weight: 600;
    cursor: pointer;
}
.sideNav li span {
    margin-left: 20px;
}

.sideNav li:hover {
    background-color: rgba(200,200,200, .2);
}

#selected {
    color:#3BC8E7 !important;
    background-color: rgba(200,200,200, .2);
}

#selected:before {
    border-radius: 22%;
    height: 20px;
    background: linear-gradient(to right,#fd0505,#3BC8E7);
    width: .1875in;
    content: "";
    position: absolute;
    left: -.75pc;
}












/* Scrollbar */
::-webkit-scrollbar-track
{
	border-radius: 10px;
	background-color: transparent;
}

::-webkit-scrollbar
{
	width: 5px;
	background-color: transparent;
}

::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	background-color: rgba(100,100,100,.3);
}

.sideNavComponent {
    display: inline-block;
    vertical-align: top;
    padding-top: 60px;
    width: 79%;
    margin-left: 100px;
}

.title {
    margin: 30px;
    color: #ffffff;
    padding-bottom: 10px;
    border-bottom: 1px solid rgb(62, 65, 109, .1);
}
.channelList {
    padding: 10px 30px;
}
.channelBody {
    display: inline-block;
    height: 150px;
    margin: 10px;
    width: 30%;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 17px;
    position: relative;
    box-shadow: 3px 2px 8px rgba(50, 50, 50, 0.4);
}

.textChannel {
    position: absolute;
    bottom: 15px;
    padding-top: 5px;
    padding-bottom: 5px;
    background-color: rgba(60, 200, 231, .7);
    color:white;
    padding-right: 20px;
    min-width: 60%;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
}
.textChannel h6 {
    margin: 0px;
    font-size: 15px;
    font-weight: 600;
    padding-left: 10px;
}
.textChannel span {
    font-size: 10px;
    padding-left: 10px;
}
.joinChannel {
    position: absolute;
    top: 15px;
    text-align: right;
    width: 100%;
}
.joinChannel button {
    padding: 10px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    border: none;
    color: white;
    align-items: center;
    display: inline-flex;
    background-color: rgba(60, 200, 231, .7);
    cursor: pointer;
}
.joinChannel button span {
    margin-left: 5px;
}
.joinChannel button:hover > span {
    text-decoration: underline;
}


.RoomComponent {
    display: inline-block;
    vertical-align: top;
    padding-top: 60px;
    width: 79%;
    margin-left: 100px;
}
